.slideshow {
    position: relative;
}

.slideshow__slides {
    @include aspectRatio(3, 1.5);
}

.slideshow__slide {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: opacity .4s;
    background: black;

    img{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &.is-portrait {
        img{
            height: 100%;
            width: auto;
        }
    }

    &.is-landscape {
        
        img {
            width: 100%;
            
            // height: 100%;
            // width: auto;
            // margin: 0 auto;
            // display: block;
        }
        
    }

    &.is-active {
        opacity: 1;
        z-index: 1;
    }

    img {
        width: 100%;
    }
}

.slideshow__buttonList {
    position: absolute;
    bottom: 20px;
    width: auto;
    padding: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;   
}

.slideshow__arrow{
    z-index: 4;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    text-indent: -9999px;
    padding: 0;
    border: 0;
    background: 0;
    svg{
        height: 50px;
        display: block;
    }
    path{
        fill: white;
    }
}

.slideshow__arrow--next {
    right: 10px;
    svg{
        // display: block;
        transform: rotate(180deg);
    }
}

.slideshow__arrow--prev {
    left: 10px;
}

.slideshow__button {
    cursor: pointer;
    height: 10px;
    width: 10px;
    border-radius: 100%;
    padding: 0;
    margin: 0 5px;
    text-indent: -99999px;
    opacity: .6;

    &.is-active {
        opacity: 1;
    }
}

